import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import HowToRepurchase from "../views/HowToRepurchase.vue";
import Repurchase from "../views/Repurchase.vue";
import ContactUs from "../views/ContactUs.vue";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/repurchase",
        name: "Repurchase",
        component: Repurchase,
    },
    {
        path: "/how-repurchase-works",
        name: "HowToRepurchase",
        component: HowToRepurchase,
    },
    {
        path: "/contact-us",
        name: "ContactUs",
        component: ContactUs,
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
    scrollBehavior(to, from, savedPosition) {
        if (to.hash) {
            return {
                el: to.hash,
                top: 0,
            };
        }
    },
});

export default router;
