<template>
    <div class="container">
        <label v-if="label">{{ label }}</label>
        <input
            type="text"
            :placeholder="placeholder"
            v-model="modelValue"
            @input="change"
        />
    </div>
</template>

<script>
export default {
    props: ["label", "placeholder", "modelValue"],
    emits: ["update:modelValue"],
    methods: {
        change(text) {
            this.$emit("update:modelValue", text);
        },
    },
};
</script>

<style scoped>
.container {
    display: block;
}
label {
    font-size: 0.85rem;
    font-weight: 700;
    margin-bottom: 5px;
}
input[type="text"] {
    font-size: 1rem;
    font-weight: 400;
    font-family: var(--font-family);
    padding: 0.75rem 0.9rem;
    border-radius: 7px;
    border: 2px solid #dadada;
    transition: 100ms ease-in-out;
    width: 100%;
}
input[type="text"]:hover {
    border-color: #539ce6;
}
input[type="text"]:focus {
    outline-color: #308ae4;
    outline-width: 2px;
    outline-style: solid;
}
</style>
