<template>
    <nav>
        <div class="content">
            <div class="logo">
                <router-link to="/">MEILLEURSTAUXBANQUE</router-link>
            </div>
            <div class="nav-links">
                <router-link class="nav-link" to="/">Acceuil</router-link>
                <router-link class="nav-link" to="/#about"
                    >A Propos</router-link
                >
                <router-link class="nav-link" to="/#services"
                    >Nos Services</router-link
                >
                <router-link class="nav-link" to="/contact-us"
                    >Contactez-Nous</router-link
                >
            </div>
            <div class="hamburger">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </div>
        </div>
    </nav>
</template>

<script>
import { onMounted } from "@vue/runtime-core";
export default {
    setup() {
        onMounted(() => {
            // Show the hamburger menu
            const hamburger = document.querySelector(".hamburger");
            const navLinks = document.querySelector(".nav-links");

            hamburger.addEventListener("click", mobileMenu);

            function mobileMenu() {
                hamburger.classList.toggle("active");
                navLinks.classList.toggle("active");
            }

            // Hide it when a navLink is clicked
            const navLink = document.querySelectorAll(".nav-link");

            navLink.forEach((n) => n.addEventListener("click", closeMenu));

            function closeMenu() {
                hamburger.classList.remove("active");
                navLinks.classList.remove("active");
            }
        });
    },
};
</script>

<style scoped>
nav {
    position: absolute;
    height: var(--nav-height);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

nav .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    width: var(--content-width);
    font-size: 1.15rem;
}

nav .content .nav-link {
    margin-left: 1.75rem;
    padding: 1rem 0;
    border-bottom: 3px solid transparent;
    font-weight: 400;
}

/* nav .content a.router-link-exact-active {
    color: var(--primary-color);
} */

nav .content .nav-link:hover {
    border-bottom-color: var(--primary-color);
    color: var(--primary-color);
}

nav .logo a {
    font-weight: 700;
    font-size: 1.25rem;
}
nav .logo a:hover {
    color: var(--primary-color);
}

nav .hamburger {
    display: none;
}

nav .hamburger .bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: white;
}

@media only screen and (max-width: 768px) {
    nav .nav-links {
        position: fixed;
        display: flex;
        flex-direction: column;
        left: -100%;
        top: 5rem;
        flex-direction: column;
        background-color: rgb(15, 15, 15);
        width: 100%;
        text-align: center;
        transition: 0.3s;
        box-shadow: 0 10px 27px rgba(0, 0, 0, 0.05);
    }

    nav .nav-links.active {
        left: 0;
    }

    nav .nav-links .nav-link {
        margin: 1rem 0;
    }
    nav .content a:hover {
        border-bottom-color: transparent;
        color: var(--primary-color);
    }

    nav .hamburger {
        display: block;
        cursor: pointer;
    }
    nav .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }
    nav .hamburger.active .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }
    nav .hamburger.active .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
}
</style>
