<template>
    <Header title="Le rachat de crédit" />

    <section data-aos="fade-up" class="about">
        <h1 class="title">Qu’est-ce que le rachat de crédits ?</h1>
        <div class="content">
            <div class="about">
                <p>
                    Le rachat de crédits, aussi connu sous le nom de
                    regroupement de crédits ou restructuration de dettes, est
                    une opération permettant de regrouper les mensualités de
                    plusieurs crédits en un crédit unique et donc, d'être
                    prélevé d'une seule mensualité.
                </p>
                <p>
                    Avec le développement d’Internet, une demande de
                    regroupement de crédits peut se faire directement en ligne.
                    Mais il est essentiel de comprendre les fondamentaux, les
                    pièges et les bonnes pratiques de cette opération.
                </p>
                <p>
                    Le principe du rachat de crédits est relativement simple :
                    les ménages endettés s’adressent à un établissement autre
                    que celui qui leur a initialement accordé les fonds.Ce
                    dernier solde l’ensemble des crédits auprès des différents
                    créanciers (que cela soit des crédits immobiliers et/ou
                    crédits à la consommation) et les remplace par un nouveau
                    contrat à un taux plus attractif mais d'une durée plus
                    longue.
                </p>
            </div>
            <img class="pic" :src="pics.about" alt="About repurchasing" />
        </div>
    </section>

    <section data-aos="fade-up" class="advantages">
        <div class="content">
            <img class="pic" :src="pics.advantages" alt="Advantages" />
            <div class="about">
                <h1 class="title">Les avantages du regroupement de crédits</h1>
                <ol>
                    <li>Une réduction significative du montant</li>
                    <li>Vous n’avez plus qu’un seul et unique interlocuteur</li>
                    <li>
                        Le rachat crédit est ouvert à toutes les personnes ayant
                        des emprunts en cours
                    </li>
                </ol>
                <p>
                    Dans le cas d’un crédit immobilier, la solution est
                    accessible aussi bien aux locataires qu’aux propriétaires de
                    leur résidence principale, ainsi qu’aux personnes hébergées
                    par un tiers ou logées par leur employeur.
                </p>
                <p>
                    Pour que ces réductions de mensualités soient possibles,
                    outre l’application d’un taux parfois plus faible, la durée
                    de remboursement est allongée. La contrepartie : le coût
                    total du crédit va augmenter proportionnellement.
                </p>
            </div>
        </div>
    </section>

    <section data-aos="fade-up" class="how-it-works">
        <div class="content">
            <div class="about">
                <h1 class="title">Comment ça marche</h1>
                <p>
                    Pour proposer un nouveau contrat qui vous permet de réduire
                    le montant de votre mensualité, l’organisme financier
                    commence par totaliser les sommes que vous devez encore au
                    titre de vos dettes en cours, qu’il s’agisse d’un prêt
                    immobilier, d’un crédit automobile, d’un prêt personnel ou
                    un autre crédit à la consommation.
                </p>
                <p>
                    Il se penche ensuite sur vos dépenses courantes prévisibles
                    et incompressibles. Ajoutées à vos remboursements, elles
                    sont déduites de vos revenus afin de connaître votre reste à
                    vivre, votre niveau d’endettement et votre capacité de
                    remboursement.
                </p>
